import uniIcon from "@/components/uni-icons/uni-icons.vue";
import topTabbar from "@/components/topTabbar-inquiry.vue";
import filterButton from "@/components/filterButton.vue";
import inquireList from "@/components/inquireList.vue";
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _this,timer;
export default {
	components:{
		uniIcon,
		topTabbar,
		filterButton,
		inquireList,
		uniLoadMore
	},
	data() {
		return {
			
			loadingType: 'more',
			
			inputValueOne:'',
			items: ['我的询价','常规询价','找样询价'],
			activeIndex: 0,
			filterButton:['近3天','近7天','近15天','一个月内'],
			fitlerButtonIndex: -1,
			count: 0,
			lists:[],
			totalPage:'',   //总页数
			
			keyword : '',			//搜索关键字
			affiliation	 : 1,	//1我的，2非我的
			inquiryType : 0,		//0全部，1常规，2找样
			time : 0,				//天为单位
			pageNum	 : 1,		//当前页数
			pageSize : 20 		,//每页记录数
			isDoRefresh:false

		};
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.getmoreInquiry();
		        }, 1000);
		
		
		
	},
	onPullDownRefresh:function(){
		
		this.getInquiryList();
	},
	onShow:function(){
		let pages = getCurrentPages();
		let currPage = pages[pages.length-1];
		if (currPage.data.isDoRefresh == true){
			       currPage.data.isDoRefresh = false;
				   this.getInquiryList();
			 }
	},
	onLoad:function(){
		_this = this
		this.getInquiryList()
	},
	methods:{
		 getmoreInquiry: function() {
				if (_this.loadingType !== 'more') {//loadingType!=0;直接返回
					return false;
				}
				_this.loadingType = 'loading';
				uni.showNavigationBarLoading();//显示加载动画
				let data ={
					keyword : _this.keyword,			//搜索关键字
					affiliation	 : _this.affiliation,	//1我的，2非我的
					inquiryType :  _this.inquiryType,		//1常规，2找样
					time : _this.time,				//天为单位
					pageNum	 : _this.pageNum,		//当前页数
					pageSize : _this.pageSize 		//每页记录数
					
				}
				let url = this.Api.buyDeputyList
			   this.myRequest(data,url,'get').then(res => {
						console.log(res);
						if (res.data.data.list.length == 0) {//没有数据
						    console.log("no data")
							_this.loadingType = '';
							uni.hideNavigationBarLoading();//关闭加载动画
							return;
						}
						_this.pageNum++;//每触底一次 page +1
						_this.lists = _this.lists.concat(res.data.data.list);//将数据拼接在一起
						_this.loadingType = 'more';//将loadingType归0重置
						uni.hideNavigationBarLoading();//关闭加载动画
					}).catch(err => {
						console.log(err)
					  uni.showToast({
						title: err.data.errMsg,
						icon: 'none'
					  });
				   })
		        },
		
		getInquiryList:function(){
			_this.pageNum = 1
			_this.fitlerButtonIndex = -1
			_this.loadingType = 'more';
			uni.showNavigationBarLoading();

			let data ={
				keyword : _this.keyword,			//搜索关键字
				affiliation	 : _this.affiliation,	//1我的，2非我的
				inquiryType :  _this.inquiryType,		//1常规，2找样
				time : _this.time,				//天为单位
				pageNum	 : _this.pageNum,		//当前页数
				pageSize : _this.pageSize 		//每页记录数
				
			}
			let url = this.Api.buyDeputyList
			this.myRequest(data,url,'get').then(res => {
			  _this.pageNum++;
			  console.log(res);
			  _this.lists = res.data.data.list
			  //未报价计数
			 
			  _this.count = res.data.data.totalCount
			  _this.totalPage = res.data.data.totalPage
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();//得到数据后停止下拉刷新

		},
		toDetail:function(arr){
		
			let id = arr[0]
			let type = arr[1]
			console.log("xunjialeixing",type)
			if (type ==1){
				uni.navigateTo({
					url: '/pages/qing-f-c/inquiryManage/inquiry-details?inquiryNumber='+ id,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}else {
				uni.navigateTo({
					url: '/pages/qing-f-c/inquiryManage/sampleInquiry/buyDeputy/inquiry-details?inquiryNumber='+ id,
					success: res => {},
					fail: () => {},
					complete: () => {}
				})
			}
		},
		tabSwitch:function(index){
			this.activeIndex = index
			console.log('activeIndex',this.activeIndex)
			switch (this.activeIndex){
				case 0: 
				   this.affiliation = 1  
				   this.inquiryType = 0
				   this.getInquiryList()
				   uni.pageScrollTo({
				   	duration: 0,
					scrollTop: 0
				   })
				   break;
				case 1: 
				   this.affiliation = 2
				   this.inquiryType = 1
				   this.getInquiryList()
				   uni.pageScrollTo({
				   	duration: 0,
				   	scrollTop: 0
				   })
				   break
				case 2: 
				   this.affiliation = 2
				   this.inquiryType = 2
				   this.getInquiryList()
				   uni.pageScrollTo({
				   	duration: 0,
				   	scrollTop: 0
				   })
				   break
			}
		},
		filterButtonChange:function(index){
			this.fitlerButtonIndex = index
			switch(index){
				case 0:
				    this.time = 3
					this.pageNum =1
				    break  
				case 1:
				    this.time = 7
					this.pageNum =1
				   break
				case 2:
				    this.time = 15
					this.pageNum =1
				   break 
				case 3:
				    this.time= 30
					this.pageNum =1
				   break 
			}
			this.getInquiryList()
			console.log("fiterButtonIndex",this.fitlerButtonIndex)
		},
		blurInput:function(e){
			//搜索询价单
		},
		toNewBuild:function(){
			uni.navigateTo({
				url: '/pages/qing-f-c/inquiryManage/inquiry-created',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		toModelSearch:function(){
			
		},
		tapSearch:function() {
			
			this.keyword = this.inputValueOne
			console.log(this.keyword)
			this.getInquiryList()
			this.inputValueOne = ''
			this.keyword = ''
		}
	}
}
